@import '../../../../assets/styles/scss/data.scss';

#MySelf{

  // === About ===
  #About{
    margin-top: -60px;

    @media ( max-width: 991px ) {
      margin-top: -200px;
    }

    .left-side{
      background: rgba(51, 51, 51, 0.75);
      height: auto;
      width: 120%;
      border: solid 1px rgba(255, 123, 0, 0.2);
      border-left: none;



      h1{
        padding: 40px;
        color: #fff;
        font-family: $text-hi;
        font-size: 35px;
        line-height: 60px;

        @media ( max-width: 991px ) {
          padding: 15px;
        }

        @media ( max-width: 767px ) {
          font-size: 28px;
          line-height: 45px;
        }

        @media ( max-width: 520px ) {
          font-size: 20px;
        }

        @media ( min-width: 1920px ) {
          margin-left: 20vw;
        }

        span.hat{

          &::after{
            content: '';
            background: url('./Sub/Img/cowboy_hat.svg') no-repeat center center;
            background-size: cover;
            position: absolute;
            top: 15px;
            width: 60px;
            height: 60px;

            @media ( max-width: 991px ) {
              top: 0;
            }

            @media ( max-width: 767px ) {
              width: 35px;
              height: 35px;
            }
          }
        }
      }
    }

    .right-side{

      p{
        color: #fff;
        font-size: 25px;
        font-family: $text-hi;
        padding: 15px;
        line-height: 40px;
        margin-top: 30px;

        @media ( max-width: 1199px ) {
          font-size: 18px;
        }

        @media ( min-width: 992px ) {
          padding: 20px 30px 10px 10px;
          max-width: 600px;
          margin-left: 30px;
        }

        @media ( max-width: 991px ) {
          font-size: 25px;
        }

        strong{
          text-decoration: underline;
        }

        em{
          padding-right: 5px;
          text-decoration: underline;
        }
      }
    }
  }


  // === Tools ===
  #Tools{
    max-width: 600px;
    margin: 0 auto;
    padding-left: 15px;
    margin-top: 120px;

    @media ( max-width: 991px ) {
      margin-top: 70px;
    }

    h2{
      color: #fff;
      z-index: 100;
      font-family: $link-text;
      font-weight: 700 !important;
      letter-spacing: 1.5px;
      font-style: italic;

        span.stroke{

          &::after{
            content: '';
            background: url('./Sub/Img/strokes/stroke_1.svg') no-repeat center center;
            background-size: cover;
            position: absolute;
            margin-top: -25px;
            margin-left: -170px;
            width: 250px;
            height: 100px;
          }
      }
    }

    .tools-block{
      margin-top: 20px;
      margin-bottom: 50px;
      padding: 0 30px 0 50px;

      @media ( max-width: 420px ) {
        padding: 0;
      }

      .tool{
        margin-top: 40px;
        a{

          img{
            width: 50px;
          }
        }
      }
    }
  }


  // === Skills ===
  #Skills{
    max-width: 1100px;
    margin: 0 auto;
    margin-top: 120px;

    h1{
      color: #fff;
      z-index: 100;
      font-family: $link-text;
      font-weight: 700 !important;
      letter-spacing: 1.5px;
      font-style: italic;

      @media ( max-width: 1199px ) {
        padding-left: 55px;
      }

        span.stroke{

          &::after{
            content: '';
            background: url('./Sub/Img/strokes/stroke_2.svg') no-repeat center center;
            background-size: cover;
            position: absolute;
            margin-top: -25px;
            margin-left: -170px;
            width: 250px;
            height: 100px;
          }
      }
    }

    .skills-block{
      color: #fff;
      max-width: 800px;
      margin: 0 auto;

      .skills{
        margin-top: 70px;
        text-align: center;

        h3{
          font-family: $header-text;
          font-weight: 700 !important;
          text-decoration: underline;
          margin-bottom: 30px;
        }

        ul{
          list-style: none;
          padding-left: 15px;

          li{
            margin-bottom: 7px;
            font-size: 22px;
            font-family: $text-hi;
            letter-spacing: 1px;
          }
        }
      }
    }
  }
}